body {
    background-color: #254159;
}

.App {
    text-align: center;
    justify-content: center;
    width: 100%;
    flex-direction: column;
    display: flex;
    height: 100%;
    font-size: calc(10px + 1vmin);
}

.content {
    color: #dddfe2;
    flex-direction: row;
}

.title {
    color: #ffffff;
}

@media screen and (max-width: 500px) {
    .App,
    .lists {
        flex-direction: column;
    }
}

a {
    color: #6abaff;
}

code {
    background-color: #33597aa6;
    padding: 0.03em 0.4em;
    border-radius: 6px;
}
